
import React, { useRef, useState } from "react";
import { DatePicker, Row, Col, Select, Button, message } from 'antd';
import moment from "moment";
import zhCN from 'antd/es/date-picker/locale/zh_CN';
import { UploadOutlined } from '@ant-design/icons';
import _ from "lodash"

const { RangePicker } = DatePicker;

const { Option } = Select;

const Header = (props) => {
    const [time, setTime] = useState([]);  //
    const [hasPro, setHasPro] = useState([]); //
    const [hasFound, setHasFound] = useState([]); //
    const [allPro, setAllPro] = useState([]); //
    const [allFound, setAllFound] = useState([]); //found
    const jsonData = useRef(null);

    const toGeneralGraph = () => {
        // 
        let json = _.cloneDeep(jsonData.current);
        if (json && json.length) {
            let data = json.filter(item => {
                let { date = '', found = [], project = '' } = item;
                let t = true;
                let p = hasPro.length ? hasPro.includes(project) : true;
                found = found.filter(fName => hasFound.length ? hasFound.includes(fName) : true);
                item.allFounds = [...item.found];
                item.found = found;
                let f = found.length > 0;
                if (hasFound.length) {
                    f = found.length === hasFound.length
                }
                if (time && time.length && (date < time[0] || date > time[1])) {
                    t = false
                };
                return t && p && f;
            });
            //    
            let nodes = [], relations = [];
            for (const item of data) {
                let { date, description, found, id, project, round } = item;
                let startNode = {}, endNode = {};
                // 
                endNode = {
                    id: project,
                    labels: ["Project"],
                    className: 'Project',
                    properties: {
                        type: 'project',
                        project,
                        label_for_match: "Project",
                        date,
                        name: project,
                        id,
                        description,
                        round
                    }
                }
                for (const foundItem of found) {
                    startNode = {
                        id: foundItem,
                        labels: ['Person'],
                        className: 'Person',
                        properties: {
                            type: 'fund',
                            label_for_match: "Person", project, name: foundItem, date
                        }
                    }
                    relations.push({
                        id: `${foundItem}-${project}`,
                        startNodeId: foundItem,
                        endNodeId: project,
                        type: 'fund to project',
                        properties: {
                            name: `${id}->${project}`
                        }
                    })
                    nodes.push(startNode);
                }

                nodes.push(endNode);
            }
            //  for (const item of data) {
            //     let {date , description , found ,  id ,  project , round} = item;
            //     if(!nodesMap[project]){
            //         nodesMap[project] = { 
            //             id : project ,  
            //             properties : { list : [] , type : 'project' , project , name : project }
            //         }
            //     }
            //     nodesMap[project].properties.list.push({
            //         id ,
            //         date ,
            //         found,
            //         description,
            //         round,
            //     })
            //      for (const foundItem of found) {
            //          if(!nodesMap[foundItem]){
            //             nodesMap[foundItem] = { 
            //                 id : foundItem , 
            //                 properties : { list : [] , type : 'found' , project , name : foundItem}
            //             }
            //          }
            //          nodesMap[foundItem].properties.list.push({
            //              id  ,
            //              date ,
            //              project,
            //              round,
            //              description
            //          })
            //      }
            //  };
            //  console.log(nodesMap)
            //  for (const key in nodesMap) {
            //      if (Object.hasOwnProperty.call(nodesMap, key)) {
            //          const nodeItem = nodesMap[key];
            //          nodes.push(nodeItem);
            //          let id = nodeItem.id;
            //          let {type ,  project} = nodeItem.properties;
            //          if(type === 'found'){
            //              relations.push({
            //                  id : `${id}-${project}`,
            //                  startNodeId : id,
            //                  endNodeId : project,
            //                  type : 'found to project',
            //                  properties : {
            //                      name : `${id}->${project}`
            //                  }
            //              })
            //          }       
            //      }
            //  }

            props.onGeneral && props.onGeneral({ nodes, relations })

        } else {
            message.error('json');
        }
    }

    const onChangeTime = (value) => {
        if (value && value.length) {
            setTime([
                moment(new Date(value[0])).format('YYYY.HH.DD'),
                moment(new Date(value[1])).format('YYYY.HH.DD')
            ])
        } else {
            setTime([])
        }
    }

    // 
    const onChangeSelectPro = (value) => {
        setHasPro(value);
    }

    // 
    const onChangeSelectFound = (value) => {
        setHasFound(value)
    }

    const onChangeFile = (event) => {
        var selectedFile = event.target.files[0];//File
        let { type } = selectedFile;
        if (type !== 'application/json') {
            message.error('!');
            return;
        }
        var reader = new FileReader();//
        reader.readAsText(selectedFile);//
        reader.onload = function () {
            toHandleJson(JSON.parse(this.result));
        };

    }

    const toHandleJson = (json) => {
        // 
        let allProject = [], allFound = [];
        for (const item of json) {
            let { date, description, found, id, project = '', round = 0 } = item || {};
            allProject.push({
                id: `${id}_${project}`,
                name: project,
                properties: { id, description, date, round }
            });
            allFound = [...new Set([...allFound, ...found])];
        }
        setAllPro(allProject);
        setAllFound(allFound);
        setHasFound([]);
        setHasPro([allProject[0].name]);
        jsonData.current = json;
    }

    return (
        <Row style={{ padding: '20px' }}>
            <Col span={4}>
                <Button icon={<UploadOutlined />} className="input-button">
                    Upload JSON
                 <input type="file" onChange={onChangeFile} />
                </Button>
            </Col>
            <Col span={4}>
                <RangePicker locale={zhCN} onChange={onChangeTime} />
            </Col>
            <Col span={12}>
                <Select
                    mode="multiple"
                    style={{ width: '40%', marginLeft: 10 }}
                    placeholder="project"
                    value={hasPro}
                    onChange={onChangeSelectPro}
                    optionLabelProp="label"
                >
                    {
                        allPro.map(item => (
                            <Option value={item.name} label={item.name} key={item.id}>
                                <div className="demo-option-label-item">
                                    {item.name}
                                </div>
                            </Option>
                        ))
                    }
                </Select>
                <Select
                    mode="multiple"
                    style={{ width: '40%', marginLeft: 15 }}
                    placeholder="fund"
                    value={hasFound}
                    onChange={onChangeSelectFound}
                    optionLabelProp="label"
                >
                    {
                        allFound.map((item, index) => (
                            <Option value={item} label={item} key={`${item}-${index}`}>
                                <div className="demo-option-label-item">
                                    {item}
                                </div>
                            </Option>
                        ))
                    }
                </Select>
            </Col>
            <Col span="2" offset="1">
                <Button type="primary" onClick={toGeneralGraph}></Button>
            </Col>
        </Row>
    )
}

export default React.memo(Header, () => true)